import * as API from './'

export default {

  //查询获取总列表
  findTOList: params => {
    return API.POST(`/filter/toatalList/${params.pageNo}//${params.role}/${params.userId}`, params.totalReport)
  },

  findSrpList: params => {
    return API.POST('/filter/srpList', params)
  },

  findDetailList: uuid => {
    return API.GET(`/filter/detailList/${uuid}`)
  },
  findmonit: monitorId => {
    return API.GET(`/filter/getMonitorById/${monitorId}`)
  },


  //增加监控项
  insertMonit: params => {
    return API.POST('/filter/monitorItemInsert', params)
  },
  //修改监控项
  changeMonit: params => {
    return API.PUT('/filter/monitorItemUpdate', params)
  },
  //删除一个监控项
  remove: params => {
    return API.DELETE('/filter/deletemonitorItemById', params)
  },





}



